.header-profile {
    border: 1px solid #DDDDDD;
    border-radius: 21px;
    cursor: pointer;
    box-shadow: 0px 3px 4px -3px rgba(0, 0, 0, 0.65);
    box-shadow: 0px 3px 4px -3px rgba(0, 0, 0, 0.65);

    min-width: 75px;
    height: 42px;
    flex: 0 1 auto;

    text-align: center;
    color: #717171;

    display: flex;
    justify-content: center;
    align-items: center;


    >img {
        background-color: currentcolor;
        border-radius: 50%;
        width: 30px;
        height: 30px;
        margin-left: 12px;
    }

}

.header-menu-false {
    display: none;
}

.header-menu-true {
    display: block;
    position: absolute;
    right: 7%;
    /* left: 10px; */
    margin-top: 1em;
    background-color: #fff;
    border: 1px solid rgb(221, 221, 221);
    border-radius: 12px;
    padding: 24px;
    width: 16%;

    .login-signup-section{
        border-bottom: 1px solid #DDDDDD;
    }

    .login-signup-section > *{
        cursor: pointer;

        &:hover{
            background-color: #DDDDDD;
        }
    }
}