.stay-filter {
  padding-top: 20px;
  background-color: white;
  top: var(--navigation-bar-offset, 80px);
  color: rgb(34, 34, 34);
  position: static;
}

.fixed {
  background-color: #fff;
  position: fixed;
  box-shadow: inset 0 -2px 0 #1e1e1e0d;
  top: 70px;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 15px;
}

.filter-container {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 1fr 100px;
  gap: 16px;
  margin: 12px 0 0;

  // >* {
  //   padding-top: 10px;
  //   padding-bottom: 10px;
  // }
}

.stay-filter-btn {
  background-color: white;
  border: 1px solid #DDDDDD;
  border-radius: 12px;
  cursor: pointer;
  margin: 0;
  appearance: none;
  display: inline-block;
  min-width: 48px;
  height: 48px;
  color: black;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-content: center;

  >div {
    display: block;
    height: 14px;
    width: 14px;
    fill: currentColor;
  }
}

.main-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0808086a;
  cursor: pointer;
  display: none;
}

.labels-container {
  // display: grid;
  // grid-template-columns: repeat(16, 1fr);
  // gap: 16px;

  >div {
    display: block;
    height: 24px;
    width: 24px;
    // fill: currentColor;
  }
}