            }

        }

        .reserve-btn-section {

            #gradientBtn {
                width: 100%;
                min-height: 3.5em;
                border-radius: 8px;

                background-image: linear-gradient(to right,
                        rgb(230, 30, 77) 0%,
                        rgb(227, 28, 95) 50%,
                        rgb(215, 4, 102) 100%);

                .gradient {
                    background-image: radial-gradient(circle at center center,
                            rgb(255, 56, 92) 0%,
                            rgb(230, 30, 77) 27.5%,
                            rgb(227, 28, 95) 40%,
                            rgb(215, 4, 102) 57.5%,
                            rgb(189, 30, 89) 75%,
                            rgb(189, 30, 89) 100%);
                    background-position: calc((100 - var(--mouse-x, 0)) * 1%) calc((100 - var(--mouse-y, 0)) * 1%);
                    background-size: 200% 200%;
                    transform: scale(1);
                }

                &:hover {
                    .gradient {
                        opacity: 1;
                    }
                }
            }
        }

        .msg-txt {
            text-align: center;
            margin-top: 0.5em;

            &>* {
                margin-top: 0.5em;
                white-space: normal;
                font-family: Circular, -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
                font-weight: 400;
                font-size: 14px;
                line-height: 18px;
            }
        }

        .calc-reserve {
            margin-top: 1.6em;

            .total-price-nights> :nth-child(1),
            .cleaning-fee> :nth-child(1),
            .service-fee> :nth-child(1) {
                text-decoration: underline;
            }

            .cleaning-fee,
            .service-fee {
                padding-top: 1em;
            }

            .total {
                .total-amount {
                    font-size: 1em;
                    line-height: 1.12em;
                    color: rgb(34, 34, 34);
                    font-family: cereal_BD;
                    border-top: 1px solid rgb(221, 221, 221);
                    margin-top: 24px;
                    padding-top: 24px;
                }


            }
        }
    }




