.select-guests {
    background: rgb(255, 255, 255);
    border-radius: 4px;
    box-shadow: rgb(0 0 0 / 15%) 0px 2px 6px, rgb(0 0 0 / 7%) 0px 0px 0px 1px;
    margin-bottom: 16px;
    min-width: 240px;
    padding: 16px;
    position: absolute;
    text-align: left;
    width: 100%;
    z-index: 999;
    right: 0px;
    left: 0px;

    .pick-guest-account {
        margin-top: 8px;
        margin-bottom: 24px;
        display: flex;
        justify-content: space-between;
        cursor: pointer;

        &> :first-child {
            >h3 {
                font-size: 16px;
                padding-bottom: 4px;
                font-weight: 600;
                margin: 0;
            }

            >h5 {
                margin: 0;
                font-size: 14px;
                font-weight: 400;
                opacity: 0.8;
            }
        }

        .choose-amount {
            display: flex;

            >div {
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 104px;
                line-height: 20px;
                height: 32px;

                >button {
                    background: none;
                    color: black;
                    border-radius: 50%;
                    width: 32px;
                    border: 1px solid black;
                    height: 32px;
                    border-color: rgb(176, 176, 176);
                    color: rgb(113, 113, 113);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }


        }
    }

    .close-btn-reserve{
        width: 100%;
        background: none;
        color: #222;
        font-weight: 600;
        text-decoration: underline;
        display: flex;
        justify-content: flex-end;
    }
}